.green-btn {
  width: 152px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #77AC57;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), -9px -9px 25px rgba(255, 255, 255, 0.25);
  border-radius: 10px;
  font-family: $font-family1;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 0.3em;
  color: #FFFFFF;
  transition: all .4s;

  &:hover {
    background: #8bc766;
    box-shadow: 0px 4px 14px 6px rgba(0, 0, 0, 0.25), -9px -9px 25px rgba(255, 255, 255, 0.25);
  }

}


.slider-nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  grid-gap: 23px;
  flex-grow: 1;

  svg {
    fill: #77AC57;
  }

  &__left {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 46px;
    height: 46px;
    border: 1px solid #77AC57;
    border-radius: 50%;
    transition: all .4s;


    &:hover {
      box-shadow: 0px 4px 14px 6px rgba(0, 0, 0, 0.25), -9px -9px 25px rgba(255, 255, 255, 0.25);
    }
  }

  &__right {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 46px;
    height: 46px;
    border: 1px solid #77AC57;
    border-radius: 50%;
    transition: all .4s;


    &:hover {
      box-shadow: 0px 4px 14px 6px rgba(0, 0, 0, 0.25), -9px -9px 25px rgba(255, 255, 255, 0.25);
    }
  }
}



